<script>
export default {
  methods: {
    animations() {
      let i = 0;
      let tumbler = 0;
      let delay = 0;
      //   let timer = setInterval(deus, 100);
      let user = this.$refs.DeusE;
      let d = user.childElementCount;

      setInterval(deus, 100);

      function deus() {
        if (delay < 100) {
          delay++;
        } else if (delay == 100) {
          if (tumbler == 0) {
            user.children[i].classList.add('DeusExx');
            i++;
          } else if (tumbler == 1) {
            user.children[i - 1].classList.remove('DeusExx');
            i--;
          }
          if (i == d) {
            tumbler++;
            delay = 0;
          }
          if (i == 0) {
            tumbler--;
            delay = 0;
          }
        }
      }
    },
  },
  mounted() {
    this.animations();
  },
};
</script>
